.card {
  border-radius: 2px;
  height: 130px;
  margin: 1rem;
  position: relative;
  width: 150px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#E4000F;
}
.card > .img-container > img {
  width: 60%;
}



